/* Register */
.form-container {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  background-color: #786467;
  background-image: linear-gradient(0deg, rgb(248, 229, 215) 0%, #ffeef8 100%);
}
.form-container form {
  padding: 36px;
  border-radius: 25px;
  box-shadow: 0 0 6px 2px rgb(221, 219, 219);
  --webkit-box-shadow: 0 0 6px 2px rgb(221, 219, 219);
  background-color: white;
}
.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
}
.form-container form button {
  border: 1px solid #786467;
  border-radius: 0;
  background-color: #ff3f6c;
  color: white;
  width: 250px;
}
input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="password"]:focus {
  border: 1px solid #f0dde0;
  box-shadow: 0 0 3px 3px #f0dde0;
}

.form-container form button:hover {
  border: 1px solid#786467;
  color: black;
  background: black;
  background: -webkit-linear-gradient(
    to right,
    rgb(248, 229, 215) 0%,
    #f0dde0 100%
  );
  background: linear-gradient(to right, rgb(248, 229, 215) 0%, #f0dde0 100%);
  /* background: -webkit-linear-gradient(to right, #f8dfe3, rgb(236, 236, 251));
  background: linear-gradient(to right, #f8dfe3, rgb(236, 236, 251));
  */
}
