/* nav bar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  height: "auto";
  width: "100%";
  font-weight: bold;
  background: linear-gradient(to bottom, #fce9ed, #f8dce0);
  line-height: 45px;
  box-shadow: 0 8px 6px -6px rgb(221, 219, 219);
  --webkit-box-shadow: 0 8px 6px -6px rgb(221, 219, 219);
}

.nav-link {
  font-weight: 300px !important;
}
.nav-link:hover {
  border-bottom: 2px solid #fb5695;
}

.navbar-brand {
  font-weight: 800;
  font-style: "roboto", "sans-serif";
  letter-spacing: 3px;
}

/*------------------------------------------------------------------*/

.display-4 {
  font-size: 100px;
  font-weight: bold;
}
.lead {
  font-size: 40px;
}
/*------------------------------------------------------------------*/
/* footer css */
.footer {
  color: white;
  padding: 25px;
  /* Gradient from top to bottom */
  background: black;
  background: -webkit-linear-gradient(to right, #786467, black);
  background: linear-gradient(to bottom, #786467, black);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: blanchedalmond;
  border-bottom: 2px solid blanchedalmond;
}
/*------------------------------------------------------------------*/
/* Contact page CSS */
.contact-img {
  width: 40%;
  height: "auto";
  padding: 0 50px;
  /* Adjust the width as needed */
}

.contact-details {
  width: 50%; /* Set a fixed width */
  height: 50%; /* Set a fixed height */
  text-align: left;
  padding: 0 20px;
}

.bg-dark {
  background: linear-gradient(to bottom, #786467, black) !important;
  border-radius: 5px;
  padding: 5px 15px; /* Adjust the padding as needed */
  margin-right: 15px;
}
.contact-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 75vh;
  padding: 0 20px; /* Adjust as needed for desired height */
}
.text-subtitle {
  font-weight: bold;
}
/*------------------------------------------------------------------*/
/* About us */
.about-container {
  display: flex;
  /*flex-direction: row;*/
  align-items: center;
  justify-content: center;
  height: 75vh;
  /*padding: 0 20px; /* Gap between the columns */
}
.about-img {
  /* width: 80%; 
  height: auto;
  padding: 0 20px;*/
  width: 50%;
  height: "auto";
  padding: 0 50px;
  /* Adjust the width as needed */
}
.about-details {
  width: 80%; /* Set a fixed width */
  height: 50%; /* Set a fixed height */
  text-align: left;
  padding: 0 50px;
}
/*------------------------------------------------------------------*/
/* Products page */
.product-link {
  text-decoration: none !important;
  color: black;
}
/*--------------------------------*/
.btn-primary {
  border: 1px solid #786467;
  border-radius: 0;
  background-color: #ff3f6c;
  color: white;
  width: 250px;
}
.btn-primary:hover,
.btn-secondary:hover {
  border: 1px solid#786467;
  color: black;
  background: black;
  background: -webkit-linear-gradient(
    to right,
    rgb(248, 229, 215) 0%,
    #f0dde0 100%
  );
  background: linear-gradient(to right, rgb(248, 229, 215) 0%, #f0dde0 100%);
  /* background: -webkit-linear-gradient(to right, #f8dfe3, rgb(236, 236, 251));
  background: linear-gradient(to right, #f8dfe3, rgb(236, 236, 251));
  */
}
.btn-secondary {
  border: 1px solid #786467;
  border-radius: 0;
  background-color: white;
  color: black;
  width: 250px;
}

.dropdown-item:hover {
  border: 1px solid #f0dde0;
  box-shadow: 0 0 3px 3px #f0dde0;
  color: black !important;
}
.dropdown-item.active {
  border: 1px solid #f0dde0;
  box-shadow: 0 0 3px 3px #f0dde0;
  background-color: #f0dde0 !important;
  color: black;
}

.list-group-item.active {
  background-color: #f0dde0 !important;
  border: 1px solid #f0dde0;
  color: black;
}
.list-group-item:hover {
  border: 1px solid #f0dde0;
  box-shadow: 0 0 3px 3px #f0dde0;
  background-color: #f0dde0 !important;
  color: black;
}
