.box2 {
  background-color: -webkit-linear-gradient(
    to right,
    rgb(248, 229, 215) 0%,
    #f0dde0 100%
  );
  background: linear-gradient(to right, rgb(248, 229, 215) 0%, #f0dde0 100%);
  color: black;
  font-weight: 700;
  font-size: 20px;
  padding: 50px 50px;
  width: 70%;
  text-decoration: none;
  display: flex;
  text-align: center;
  flex-direction: column; /* Ensure text alignment in a column */
  justify-content: center; /* Vertically center text */
  align-items: center;
  border-radius: 5px;
  border: 5px solid#f67d99;
}
.box2:hover {
  background: #f67d99;
  color: white;
}
